import { useEffect, useState } from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faGlobe} from '@fortawesome/free-solid-svg-icons';
import ReactToolTip from 'react-tooltip';

import './css/MainPage.css';

import langJson from '../json/language.json';
import userJpg from '../imgs/user.jpg';
import heFlag from '../imgs/he_flag.png';
import enFlag from '../imgs/en_flag.png';
import url1Pic from '../imgs/url1.png';
import url2Pic from '../imgs/url2.png';
import url3Pic from '../imgs/url3.png';
import url4Pic from '../imgs/url4.png';
import url5Pic from '../imgs/url5.png';
import url6Pic from '../imgs/url6.png';
import url7Pic from '../imgs/url7.jpg';

function MainPage() {
    const [lang, setLang] = useState('he');
    
    const globeIcon = () => {
        return <FontAwesomeIcon dir={langJson[lang][0]} icon={faGlobe} style={{transform: `translate(${lang == 'en' ? '-' : ''}150%, 35%)`, width: '16px'}} className="ml-1 position-absolute"/>;
    }

    return (
        <div className="customText">
            <div className="bg-info w-100">
                <div style={{height: '225px'}} className="d-flex" dir={langJson[lang][0]}>
                    <img type="button" className="position-absolute langBtn" style={{right: lang == 'he' && '20px', left: lang == 'en' && '20px', top: '20px'}} onClick={() => setLang(lang == 'en' ? 'he' : 'en')} src={lang == 'en' ? enFlag : heFlag}/>
                    <img src={userJpg} className="m-3" style={{borderRadius: '50%'}}/>
                    <table className={langJson[lang][1]} style={{height: '112.5px', transform: 'translateY(56.25px)'}}>
                        <tbody>
                            <tr className="titleText">{`${langJson[lang][2]}`}: <b>{`${langJson[lang][3]}`}</b></tr>
                            <tr className="titleText">{`${langJson[lang][4]}:`} <b>{`${langJson[lang][5]}`}</b></tr>
                            <tr className="titleText">{`${langJson[lang][6]}:`} <b>{`${langJson[lang][7]}`}</b></tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div style={{border: '1px solid', borderImageSlice: 10, borderWidth: '10px', borderImageSource: 'linear-gradient(to bottom, #17a2b8, #0d0d0d)'}}></div>
            <div dir={langJson[lang][0]} className={`${langJson[lang][1]} mx-3`}>
                <div style={{whiteSpace: 'pre-wrap'}}>{`${langJson[lang][16]}:`}</div>
                <ul>
                    <li className="font-weight-bold"><span dir="ltr">HTML</span></li>
                    <li className="font-weight-bold"><span dir="ltr">CSS</span></li>
                    <li className="font-weight-bold"><span dir="ltr">JavaScript</span></li>
                    <li className="font-weight-bold"><span dir="ltr">Java</span></li>
                    <li className="font-weight-bold"><span dir="ltr">C#</span></li>
                    <li className="font-weight-bold"><span dir="ltr">Python</span></li>
                </ul>
                <div style={{whiteSpace: 'pre-wrap'}}>{`${langJson[lang][17]} `}<b>Redux, three.js, Node.js, Express.js, MongoDB</b>{` ${langJson[lang][18]}`}</div>
                <div style={{whiteSpace: 'pre-wrap'}}>{`${langJson[lang][19]} `}<b>React.js</b>{` ${langJson[lang][20]} `}<b>Ionic Capacitor</b>{` ${langJson[lang][21]}`}</div>
                <div style={{whiteSpace: 'pre-wrap'}}>{`${langJson[lang][22]} `}<b>Unity</b>{` ${langJson[lang][23]}`}</div>
                <br/>
                <div style={{whiteSpace: 'pre-wrap'}}>{langJson[lang][24]}</div>
            </div>
            <hr style={{border: '1px solid black'}}/>
            <div dir={langJson[lang][0]} className={`mx-4 ${langJson[lang][1]}`}>
                <p style={{fontSize: '28px', fontWeight: '500'}}><u>{`${langJson[lang][8]}:`}</u></p>
                <ul className="list-unstyled p-0">
                    <li data-tip data-for="url1"><a target="_blank" href="https://saharzzz.github.io/ecl/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][9]}.</a></li>
                    <ReactToolTip id="url1">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                            </ul>
                            <img src={url1Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url2"><a target="_blank" href="https://saharzzz.github.io/shoppinglistreact/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][10]}.</a></li>
                    <ReactToolTip id="url2">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                                <li className="p-0 m-1">{langJson[lang][27]}</li>
                                <li className="p-0 m-1">{langJson[lang][28]}</li>
                            </ul>
                            <img src={url2Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url3"><a target="_blank" href="https://saharzzz.github.io/sapp/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][11]}.</a></li>
                    <ReactToolTip id="url3">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                                <li className="p-0 m-1">{langJson[lang][27]}</li>
                                <li className="p-0 m-1">{langJson[lang][28]}</li>
                            </ul>
                            <img src={url3Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url4"><a target="_blank" href="https://saharzzz.github.io/notes/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][12]}.</a></li>
                    <ReactToolTip id="url4">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                            </ul>
                            <img src={url4Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url5"><a target="_blank" href="https://saharzzz.github.io/tictactoemultiplayer/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][13]}.</a></li>
                    <ReactToolTip id="url5">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                                <li className="p-0 m-1">{langJson[lang][27]}</li>
                            </ul>
                            <img src={url5Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url6"><a target="_blank" href="https://saharzzz.github.io/dbzgame/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][14]}.</a></li>
                    <ReactToolTip id="url6">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                                <li className="p-0 m-1">{langJson[lang][27]}</li>
                            </ul>
                            <img src={url6Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                    <li data-tip data-for="url7"><a target="_blank" href="https://masaryk-square-exhibition.s3.eu-central-1.amazonaws.com/index.html" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][15]}.</a></li>
                    <ReactToolTip id="url7">
                        <div>
                            <h5>{`${langJson[lang][25]}:`}</h5>
                            <ul className="p-0 m-1">
                                <li className="p-0 m-1">{langJson[lang][26]}</li>
                            </ul>
                            <img src={url7Pic} style={{width: '300px'}}/>
                        </div>
                    </ReactToolTip>
                </ul>
            </div>
        </div>
    )
}

export default MainPage;